
 #checkText {
    position:absolute;
    display:inline-block;
    right:40px; top:12px;
    color:rgba(255,255,255,.5);
    letter-spacing:2px;
    font-size:16px;
 }
 
 #check {
    position:absolute;
    right:20px; top:20px;
 }
 
 #curvnum {
    position:absolute;
    bottom:0;
    right:0;
    width:200px;
    height:50px;
    opacity:.5;
    background:rgba(0,0,0,.6);
    transition:all .3s linear;
 }
 
 #curvnum:hover {
    opacity:1;
 }
 
 #range {
    display:block;
    width:80%;
    height:40px;
    position:relative;
    margin:0px auto;
 }
 
 #range:hover {
    cursor:pointer;
 }
 
 #rotate{
    position:absolute;
    top:50px; right:20px;
 }
 
 #rotateText {
    position:absolute;
    top:42px; right:40px;
    font-size:16px;
    color:rgba(255,255,255,.5);
 }
 
 #rangeText {
    display:block;
    width:100%;
    text-align:center;
    color:rgba(255,255,255,1);
    background:#000;
    font-size:12px;
    position:relative;
    top:5px;
 }